.react-dom-markdown-editor {
  * {
    box-sizing: border-box;
  }

  > .react-dom-markdown-editor-workspace {
    display: flex;
    flex-direction: row;
  }

  .react-dom-markdown-editor-textarea,
  .react-dom-markdown-editor-preview {
    flex: 1;
    border: 1px solid rgb(228, 228, 228);
    padding: 10px;
    overflow: auto;
  }

  .react-dom-markdown-editor-textarea {
    width: 100%;
    box-sizing: border-box;
  }
}
